let R = require('ramda');
let utils = require('../utils');

/**
 * Takes 2 or more clauses and combines them with AND
 * but negates the second (and subsequents) one
 * (this is the default fuse behaviour).
 * @param  {...[Object, String]} facet, value, clause operator
 * @return {String}           A valid Fuse AND_NOT Expression
 */
function andNot(...clauses) {
  let query = '&(';
  let negatedClauses = utils.negateAllButFirst(clauses);
  let queryBody = R.reduce(utils.concatExpressions, '', negatedClauses);

  if(clauses.length < 2) {
    throw new Error('2 or more parameters should be passed');
  }

  query = `${query}${queryBody}\ )`;

  return query.trim();
}

module.exports = andNot;



