let objectAssign = require('object-assign');
let base = require('./base');
let contents = require('./contents');
let facets = require('./facets');
let search = require('./search');

function createSearchQuery(queryObj={}) {
  let obj = Object.create(objectAssign({}, base, facets, contents, search));

  obj.query = queryObj;

  return obj;
}

module.exports = createSearchQuery;



