let objectAssign = require('object-assign');
let base = require('./base');
let contents = require('./contents');

function createContentsQuery(queryObj={}) {
  let obj = Object.create(objectAssign(contents, base));

  obj.query = queryObj;

  return obj;
}

module.exports = createContentsQuery;



