let objectAssign = require('object-assign');
let base = require('./base');
let singleFacet = require('./singleFacet');

function createSingleFacetQuery(queryObj={}) {
  let obj = Object.create(objectAssign(singleFacet, base));

  obj.query = queryObj;

  return obj;
}

module.exports = createSingleFacetQuery;



