let objectAssign = require('object-assign');
let base = require('./base');
let facets = require('./facets');

function createFacetsQuery(queryObj={}) {
  let obj = Object.create(objectAssign(facets, base));

  obj.query = queryObj;

  return obj;
}

module.exports = createFacetsQuery;



